.sidebar {
    display: flex;
    flex-direction: column;
    flex: 0.25;
    height: 100vh;  
    background-color: #2f3235;
}

.sidebar__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: 2f3235;
    color: white;
    border-bottom: 3px solid #26282c;
}

.sidebar_channels {
    flex: 1;
}

.sidebar_addchannel {
    cursor: pointer;
}

.sidebar_addchannel:hover {
    color: white;
}

.sidebar_channelsHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #2f3235;
    color: gray;
}

.sidebar_header {
    display: flex;
    align-items: center;
}

.sidebar_voice, .sidebar_profile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: gray;
    padding: 10px;
    border-top: 1px solid gray;
}

.sidebar_voiceInfo {
    flex: 1;
    padding: 10px;
}

.sidebar_voiceIcon {
    color: #4fb185;
}

.sidebar_voiceInfo > h3 {
    color: #4fb185;
}
 
.sidebar_voiceInfo > p {
    font-size: smaller;
}

.sidebar_voiceIcons > .MuiSvgIcon-root {
    padding: 10px;
}

/* .sidebar_voiceIcons {
    display: flex;
    flex-direction: column;
} */

.sidebar_profileInfo {
    padding: 10px;
    flex: 1;
}

.sidebar_profileInfo > h3 {
    color: white;
}

.sidebar_profileIcons > .MuiSvgIcon-root {
    padding: 10px;
}

